import React, { Component } from 'react';
import logo from '../../logo.svg';
import '../../css/login/login.css';
import { HashRouter as Router,Link} from 'react-router-dom';
import PropTypes from "proptypes";
import RequestUtil from '../commom/requestUtil';

class Login extends Component {

    constructor(props){
        super(props);
    }

    loginClick(event) {
        var params = {};
        params.username = document.getElementById('username').value;
        params.password = document.getElementById('password').value;
        RequestUtil.post("user/login",params,function (reply) {
            if(reply.resultCode == '0'){
                if(reply.data.resultCode == '0'){
                    Login.that.context.router.history.push({
                        pathname: '/home',
                        data: reply.data.resultObject
                    });
                } else{
                    alert(reply.data.resultMsg);
                }
            } else{
                alert("登录请求异常");
            }
        });
    }
    render(){
        Login.that = this;
        var styleData = {
            color:'blue',
            position: 'relative',
            marginTop: '20px'
        }
        var loginFormDivStyle = {
            paddingTop:'20px'
        }
        var aStyle={
            cursor:'pointer'
        }
        return(
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo" />
                    <h1 className="App-title">Welcome to MDOD</h1>
                </header>
                <div id = 'loginForm' style={styleData}>
                    <div style={loginFormDivStyle}>
                        <label>用户名：</label>
                        <input id='username' type='text'/>
                    </div>
                    <div style={loginFormDivStyle}>
                        <label>密码：</label>
                        <input id='password' type='password'/>
                    </div>
                    <div style={loginFormDivStyle}>
                        <button onClick={this.loginClick}>登录</button>
                        <a style={aStyle}><Link to={'/register'}>点击注册</Link></a>
                    </div>
                </div>
            </div>)
    }
}
Login.contextTypes = {
    router: PropTypes.object.isRequired
};
export {Login};


