import React, { Component } from 'react';
import logo from '../../logo.svg';
import '../../css/login/login.css';

class Register extends Component{

    constructor(props){
        super(props);
    }

    register(event){

    }

    render(){
        var styleData = {
            color:'blue',
            position: 'relative',
            marginTop: '20px'
        }
        var loginFormDivStyle = {
            paddingTop:'20px'
        }
        var aStyle={
            cursor:'pointer'
        }
        return(
            <div className="App">
                <header className="App-header">
                    <img src={logo} className="App-logo" alt="logo" />
                    <h1 className="App-title">Welcome to MDOD</h1>
                </header>
                <div id = 'registerForm' style={styleData}>
                    <div style={loginFormDivStyle}>
                        <label>用户名：</label>
                        <input id='username' type='text'/>
                    </div>
                    <div style={loginFormDivStyle}>
                        <label>密码：</label>
                        <input id='password' type='password'/>
                    </div>
                    <div style={loginFormDivStyle}>
                        <label>确认密码：</label>
                        <input id='repassword' type='password'/>
                    </div>
                    <div style={loginFormDivStyle}>
                        <button onClick={this.register}>注册</button>
                        <button onClick={this.register}>返回</button>
                    </div>
                </div>
            </div>)
    }
}
export {Register};


