import jquery from 'jquery';

const RequestUtil = {
    post(url,params,callback){
        jquery.post(url,params,function(result,status){
            var reply = {};
            if(status == 'success'){
                reply.resultCode = 0;
                reply.data = result;
            } else{
                reply.resultCode = -1;
            }
            callback(reply);
        });
    },
    get(url,callback){
        jquery.get(url,function(result,status){
            var reply = {};
            if(status == 'success'){
                reply.resultCode = 0;
                reply.data = result;
            } else{
                reply.resultCode = -1;
            }
            callback(reply);
        });
    }
}
export default RequestUtil;


